@import '../theme.scss';

$max-width: 1000px;

.projects-module {
    min-height: 100vh;
    width: 100%;
    .project-screen-title {
        max-width: $max-width;
        white-space: nowrap;
        &::after {
            content: '';
            display: block;
            height: 1px;
            opacity: 0.3;
            @apply ml-4 w-full bg-white-light;
        }
        @apply w-full  md:text-3xl text-2xl  font-bold flex flex-row items-center pb-12 text-white;
    }
    .featured-projects {
        max-width: $max-width;
        @apply w-full;
    }
    .other-projects {
        max-width: $max-width;
        grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
        @apply grid gap-4 w-full;
    }
    @apply flex flex-col items-center justify-center;
}
