@import '../theme.scss';
.links-wrapper {
    position: fixed;
    @apply text-lg flex flex-col items-center justify-center bottom-0 left-6;
    .icon {
        transition: all 0.3s;
        cursor: pointer;
        &:hover {
            @apply mb-4 mt-2 text-primary;
        }
        &.disabled {
            cursor: not-allowed;
        }
    }
    &::after {
        content: '';
        display: block;
        width: 1px;
        height: 90px;
        margin: 0px auto;
        @apply bg-white-thin;
    }
}
