@import '../theme.scss';
.contact-module {
    min-height: 100vh;
    width: 100%;
    .contact-wrapper {
        max-width: 550px;
        .title {
            white-space: nowrap;
            &::after {
                content: '';
                display: block;
                height: 1px;
                opacity: 0.3;
                @apply ml-4 w-full bg-white-light;
            }
            @apply w-full md:text-3xl text-2xl font-bold flex flex-row items-center pb-8 pr-4 text-white;
        }
        .content {
            @apply text-base font-light text-center text-white;
        }
        .footer {
            .technologies-list {
                display: grid;
                grid-template-columns: repeat(2, minmax(150px, 200px));
                li {
                    @apply text-sm pl-5 flex items-center;
                    font-family: monospace;
                    position: relative;
                    &::before {
                        position: absolute;
                        content: '▹';
                        left: 0px;
                        line-height: 12px;
                        @apply text-xs text-primary;
                    }
                    @apply pb-1;
                }
            }
            @apply text-white-light;
        }
        @apply flex flex-col items-center;
    }
    @apply flex flex-col items-center justify-center;
}
